import React from 'react'
import TplGlobal from './template-global'
import BlockInfo from '../blocks/blockInfo'
import BlockProject from '../blocks/blockProject'
import Interests from '../blocks/interests'
import Input from '../form/input'
import Button from '../form/button'
import {mockProjects, } from '../mocks/mocks'
import * as users from '../mocks/data/users'
import BlockUsers from '../blocks/users'
import BlockInterests from '../blocks/interests'
import ProjectTypeIconContainer from '../icons/containers/projectType'
import BlockSocial from '../blocks/social'
import BlockFeature from '../blocks/feature'

const TplList = (props) => {
    const {title, children} = props;

    const eposters = [
        {
            type: 1,
            text: "Acetaminophen/Paracetamol for pain during pregnancy: Good or bad?",
            social: {
                nbComments: 15,
                nbVotes: {
                    up: 45,
                    down: 1
                },
                nbShared: 10,
                levels: [{type:1, nb:3}, {type:2, nb:1}, {type:3, nb:1}, {type:4, nb:4}],
                }
        },
        {
            type: 2,
            text: "Who is Galen of Pergamon, the physician philosopher?",
            social: {
                nbComments: 21,
                nbVotes: {
                    up: 30,
                    down: 2
                },
                nbShared: 8,
                levels: [{type:1, nb:5}, {type:2, nb:1}, {type:3, nb:1}, {type:4, nb:0}],
                }
        },
        
        {
            type: 1,
            text: "Breast cancer development and cosmetic hair products: Only the facts",
            social: {
                nbComments: 26,
                nbVotes: {
                    up: 67,
                    down: 6
                },
                nbShared: 15,
                levels: [{type:1, nb:2}, {type:2, nb:2}, {type:3, nb:2}, {type:4, nb:3}],
                }
        },
        
        {
            type: 1,
            text: "Living with Diabetes Type 1 at 13 y.o.: Controlling glycemia on a physical training day",
            social: {
                nbComments: 37,
                nbVotes: {
                    up: 45,
                    down: 2
                },
                nbShared: 43,
                levels: [{type:1, nb:5}, {type:2, nb:4}, {type:3, nb:1}, {type:4, nb:0}],
                }
        },
    ]

    return (
    

        <TplGlobal title={title}>
            <div className="main">
               <div className="sections">

                    <div className="section list">
                        
                        {children}

                    </div>
                    <div className="section blocks">
                        
                        <BlockInfo title="Top ePosters">
                            {
                                eposters.map((item, idx)=> {
                                    return (
                                        <div className="globalList">
                                            <div className="ePoster">
                                                <div>
                                                    <ProjectTypeIconContainer type={item.type} small />
                                                </div> 
                                                <div>
                                                    {item.text}
                                                </div>

                                            </div>
                                            <div className="social">
                                                
                                                <BlockSocial social={item.social}/>
                                            </div>
                                        </div>
                                    )
                                })
                            }
                        </BlockInfo>
                        
                        <BlockInfo title="Featured" last>
                        
                            <BlockFeature />

                        </BlockInfo>

                    </div>



               </div>
               
            </div>

                <style jsx>
                {`  
                   .globalList {
                       margin-bottom: 25px;
                       border-bottom: 1px solid white;
                   }
                   .ePoster {
                       display: flex;
                       align-items: center;

                       margin-top: 20px;
                       padding-bottom: 20px;
                   }
                   .ePoster div {
                       margin-right: 5px;
                   }
                   .social {
                       margin-bottom: 10px;
                   }
                   .main {
                     padding-left: var(--main-margin);
                    }

                    .sections {
                        display: flex;
                        flex-direction: row;


                    }

                    .section {
                        width: 260px;
                    }
                   
                    .list {
                        display: flex;
                        flex-grow: 2;
                        margin-right: 25px;
                        flex-direction: column;

                    }

                    .blocks {
                    }

                   
                  
                `}

                </style>
        </TplGlobal>

        
    )
}

export default TplList;